[href] {
  background-image: var(--underline-img), var(--underline-img);
  background-position: 50% calc(50% + 0.65625em), 50% calc(50% + 0.65625em);
  background-repeat: no-repeat, no-repeat;
  transition: background-size 0s;
  background-size: 0 0.125em, 100% 0.0625em;
}

[target="_blank"]::after {
  content: "";
  flex: 0 0 auto;
  display: inline-block;
  vertical-align: middle;
  background-color: currentColor;
  width: var(--icon-size);
  height: var(--icon-size);
  -webkit-mask-size: 100% 100%;
  mask-size: 100% 100%;
  margin-left: 0.25rem;
  margin-bottom: 0.25rem;
  --icon-size: 1rem;
  content: var(--link-blank-content);
  -webkit-mask-image: url("../icons/system/external-link-line.svg");
  mask-image: url("../icons/system/external-link-line.svg");
}

.fr-raw-link {
  --underline-img: none;
  --link-blank-content: none;
}

.fr-reset-link {
  --underline-img: none;
  --link-blank-content: none;
  text-decoration: underline;
  text-underline-offset: 2px;
}

button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  margin: 0;
  background-color: transparent;
  font-family: inherit;
}

input,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  font-family: "Marianne", arial, sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  border-radius: 0;
  border: 0;
  background-color: transparent;
  margin: 0;
}

input[type="checkbox"],
input[type="radio"] {
  -webkit-appearance: auto;
  -moz-appearance: auto;
  appearance: auto;
}

*:focus,
*:focus-visible {
  z-index: 500;
}

a,
button,
input,
input[type="checkbox"] + label::before,
input[type="radio"] + label::before,
input[type="button"],
input[type="image"],
input[type="reset"],
input[type="submit"],
select,
textarea,
[tabindex],
video[controls],
audio[controls],
[contenteditable]:not([contenteditable="false"]),
details,
details > summary:first-of-type {
  outline-offset: 2px;
  outline-width: 2px;
  outline-color: #0a76f6;
}

a:focus,
button:focus,
input:focus,
input[type="checkbox"]:focus + label::before,
input[type="radio"]:focus + label::before,
input[type="button"]:focus,
input[type="image"]:focus,
input[type="reset"]:focus,
input[type="submit"]:focus,
select:focus,
textarea:focus,
[tabindex]:focus,
video:focus[controls],
audio:focus[controls],
[contenteditable]:not([contenteditable="false"]):focus,
details:focus,
details > summary:first-of-type:focus {
  outline-style: solid;
}

a:focus:not(:focus-visible),
button:focus:not(:focus-visible),
input:focus:not(:focus-visible),
input[type="checkbox"]:focus:not(:focus-visible) + label::before,
input[type="radio"]:focus:not(:focus-visible) + label::before,
input[type="button"]:focus:not(:focus-visible),
input[type="image"]:focus:not(:focus-visible),
input[type="reset"]:focus:not(:focus-visible),
input[type="submit"]:focus:not(:focus-visible),
select:focus:not(:focus-visible),
textarea:focus:not(:focus-visible),
[tabindex]:focus:not(:focus-visible),
video:focus:not(:focus-visible)[controls],
audio:focus:not(:focus-visible)[controls],
[contenteditable]:not([contenteditable="false"]):focus:not(:focus-visible),
details:focus:not(:focus-visible),
details > summary:first-of-type:focus:not(:focus-visible) {
  outline-style: none;
}

a:focus-visible,
button:focus-visible,
input:focus-visible,
input[type="checkbox"]:focus-visible + label::before,
input[type="radio"]:focus-visible + label::before,
input[type="button"]:focus-visible,
input[type="image"]:focus-visible,
input[type="reset"]:focus-visible,
input[type="submit"]:focus-visible,
select:focus-visible,
textarea:focus-visible,
[tabindex]:focus-visible,
video:focus-visible[controls],
audio:focus-visible[controls],
[contenteditable]:not([contenteditable="false"]):focus-visible,
