a,
button,
input[type="checkbox"],
input[type="checkbox"] + label,
input[type="radio"],
input[type="radio"] + label,
input[type="file"],
input[type="range"],
input[type="button"],
input[type="image"],
input[type="reset"],
input[type="submit"],
select,
textarea,
video[controls],
